
.simple-keyboard {
  max-width: 300px;
}

.simple-keyboard.hg-theme-default.myTheme {
  border: 5px solid rgb(255, 0, 0, 0.7);
  border-radius: 10px;
  margin: 10px;
  width: calc(100% - 20px);
}

.simple-keyboard.hg-layout-default .hg-button {
    height : 25px;
}

.simple-keyboard.hg-layout-default .hg-button.hg-yellow {
    background: yellow;
    color: black;
}

.simple-keyboard.hg-layout-default .hg-button.hg-green {
    background: rgba(0, 255, 0, 0.7);
    color: black;
}

.simple-keyboard.hg-layout-default .hg-button.hg-grey {
    background: lightgrey;
    color: black;
}
